.contacts__input-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;
}
@media (max-width: 1199px) {
  .contacts__input-wrapper {
    gap: 60px;
  }
}
@media (max-width: 767px) {
  .contacts__input-wrapper {
    grid-template-columns: 1fr;
    gap: 32px;
  }
}
.contacts__input-wrapper input {
  background-color: transparent;
  border: none;
  border-bottom: 1px solid #74819c;
  width: 100%;
  padding: 0;
  padding-bottom: 6px;
  color: #74819c;
}
@media (max-width: 1199px) {
  .contacts__input-wrapper input {
    padding-bottom: 4px;
  }
}
@media (max-width: 767px) {
  .contacts__input-wrapper input {
    padding-bottom: 6px;
  }
}
.contacts__input-wrapper input::placeholder {
  color: #ffffff;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 14px;
}
@media (hover: hover) {
  .contacts__input-wrapper input:hover:not(.focus-visible) {
    border-color: #d10a42;
  }
  .contacts__input-wrapper input:hover:not(.focus-visible)::placeholder {
    color: #d10a42;
  }
}
.contacts__input-wrapper input.focus-visible:focus {
  border-color: #d10a42;
}
.contacts__input-wrapper input.focus-visible:focus::placeholder {
  color: #d10a42;
}

.contacts__btn {
  color: #ffffff;
  border: 3px solid #ffffff;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  padding: 17px;
  margin-top: 28px;
}
@media (max-width: 767px) {
  .contacts__btn {
    margin-top: 37px;
  }
}
@media (hover: hover) {
  .contacts__btn:hover:not(.focus-visible) {
    background-color: #d10a42;
  }
}
.contacts__btn.focus-visible:focus {
  background-color: #d10a42;
}

.contacts__content {
  border-top: 1px solid #74819c;
  padding: 56px 99px 65px 120px;
}
@media (max-width: 1199px) {
  .contacts__content {
    border: none;
    padding: 59px 103px 62px 133px;
  }
}
@media (max-width: 767px) {
  .contacts__content {
    padding: 70px 15px 63px;
  }
}
.contacts__content h2 {
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-size: 54px;
  font-weight: 600;
  line-height: 65px;
  letter-spacing: 0.03em;
  margin: 0;
  margin-bottom: 36px;
}
@media (max-width: 1199px) {
  .contacts__content h2 {
    font-size: 42px;
    line-height: 58px;
    margin-bottom: 30px;
  }
}
@media (max-width: 767px) {
  .contacts__content h2 {
    font-size: 32px;
    line-height: 25px;
    margin-bottom: 43px;
  }
}

.contacts__list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-auto-flow: row;
  position: relative;
  margin-left: 4px;
  gap: 18px 120px;
  max-width: 461px;
}
@media (max-width: 1199px) {
  .contacts__list {
    margin: 0;
    gap: 21px 96px;
    max-width: none;
  }
}
@media (max-width: 767px) {
  .contacts__list {
    grid-template-columns: 1fr;
  }
}
.contacts__list::before {
  content: "";
  position: absolute;
  width: 1px;
  height: 100%;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  background-color: #74819c;
}
@media (max-width: 767px) {
  .contacts__list::before {
    display: none;
  }
}
.contacts__list .contacts__item--schedule {
  grid-column: 1/2;
}
@media (max-width: 767px) {
  .contacts__list .contacts__item--schedule {
    grid-column: auto;
  }
}
.contacts__list .contacts__item--phone {
  grid-column: 2/3;
  grid-row: 1/2;
}
@media (max-width: 767px) {
  .contacts__list .contacts__item--phone {
    grid-column: auto;
    grid-row: auto;
  }
}
.contacts__list h3 {
  font-size: 16px;
  font-weight: 700;
  margin: 0;
  margin-bottom: 3px;
}
@media (max-width: 1199px) {
  .contacts__list h3 {
    font-size: 20px;
    line-height: 27px;
    margin-bottom: 7px;
  }
}
.contacts__list p {
  font-size: 14px;
  line-height: 21px;
  margin: 0;
}
@media (max-width: 1199px) {
  .contacts__list p {
    font-size: 18px;
    line-height: 27px;
  }
}
.contacts__list a {
  color: #102654;
}
@media (hover: hover) {
  .contacts__list a:hover:not(.focus-visible) {
    color: #d10a42;
  }
}
.contacts__list a.focus-visible:focus {
  color: #d10a42;
}

.footer {
  background-color: #0d031a;
}
.footer__container {
  display: flex;
  justify-content: space-between;
  padding-top: 40px;
  padding-bottom: 32px;
}
@media (max-width: 1199px) {
  .footer__container {
    padding-top: 45px;
    padding-bottom: 44px;
  }
}
@media (max-width: 767px) {
  .footer__container {
    padding-top: 45px;
    padding-bottom: 52px;
    flex-direction: column;
    position: relative;
  }
}

.footer__logo {
  margin-top: 4px;
  margin-right: 39px;
}
@media (max-width: 1199px) {
  .footer__logo svg {
    width: 82px;
    height: 18px;
  }
}
@media (max-width: 767px) {
  .footer__logo {
    position: absolute;
  }
  .footer__logo svg {
    width: 68px;
    height: 15px;
  }
}
@media (hover: hover) {
  .footer__logo:hover:not(.focus-visible) {
    color: #d10a42;
  }
}
.footer__logo.focus-visible:focus {
  color: #d10a42;
}

.footer__nav-list {
  display: flex;
  flex-wrap: wrap;
  margin: 0 auto;
}
@media (max-width: 767px) {
  .footer__nav-list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-auto-flow: row;
    gap: 16px 30px;
    margin: 0;
    margin-bottom: 33px;
  }
}
.footer__nav-list li:not(:last-child) {
  margin-right: 41px;
}
@media (max-width: 1199px) {
  .footer__nav-list li:not(:last-child) {
    margin-right: 30px;
  }
}
@media (max-width: 767px) {
  .footer__nav-list li:not(:last-child) {
    margin: 0;
  }
}
.footer__nav-list a {
  font-size: 14px;
  line-height: 16px;
}
@media (max-width: 767px) {
  .footer__nav-list a {
    font-size: 16px;
    line-height: 19px;
  }
}

.footer__item--services {
  grid-row: 2/3;
}
.footer__item--memberships {
  grid-row: 3/4;
}
.footer__item--stocks {
  grid-column: 2/3;
}

.footer__social-list {
  display: grid;
  grid-template-columns: repeat(3, auto);
  gap: 11px;
  margin-left: 30px;
}
@media (max-width: 1199px) {
  .footer__social-list {
    margin-right: -6px;
  }
}
@media (max-width: 767px) {
  .footer__social-list {
    margin: 0;
    align-self: start;
  }
}

@media (hover: hover) {
  .footer a:hover:not(.focus-visible) {
    color: #d10a42;
  }
}
.footer a.focus-visible:focus {
  color: #d10a42;
}

